<template>
  <div class="container">
    <!-- 左边：代码编辑器 -->
    <div class="editor-section">
      <div class="editor-header">
        {{title}}
      </div>
      <editor v-model="content" @init="editorInit" lang="html" theme="chrome" width="500" height="300"></editor>
    </div>
    
    <!-- 右边：组件展示 -->
    <div class="player-section">
      <div style="margin: auto;">
        <!-- <XgplayerFlvPlayer></XgplayerFlvPlayer> -->
        <div style="margin: auto;">
    <component :is="compiledComponent"></component>
  </div>
      </div>
      
    </div>
  </div>
</template>

<script>
import XgplayerFlvPlayer from './XgplayerFlvPlayer.vue';
import Vue from 'vue';

export default {
  name: 'CodeEdit',
  components: {
    editor: require('vue2-ace-editor'),
    XgplayerFlvPlayer
},
  data(){
    return{
      title: '编译器demo',
      compiledComponent: null,
      content: `
<template>
  <div id="vs"></div>
</template>


import Player1 from 'xgplayer';

export default {
  props: {
    videoUrl: {
      type: String,
      required: true,
    },
  },
  methods: {
  },
  mounted() {
    const player = new Player1({
      id: 'vs',
      url: this.videoUrl || 'http://s2.pstatp.com/cdn/expire-1-M/byted-player-videos/1.0.0/xgplayer-demo.mp4'
    });
    console.log(player);
  }
};

<style>
/* 可能还有一些样式代码，如果该组件有的话 */
</style>
`
    }
  },
  methods:{
    editorInit: function () {
            require('brace/ext/language_tools') //language extension prerequsite...
            require('brace/mode/html')                
            require('brace/mode/javascript')    //language
            require('brace/mode/less')
            require('brace/theme/chrome')
            require('brace/snippets/javascript') //snippet
        },
        compileCode() {
    const res = Vue.compile(this.content);
    this.compiledComponent = {
      render: res.render,
      staticRenderFns: res.staticRenderFns
    };
  }
  },
  created(){
  },
  watch: {
  content: {
    handler: function() {
      this.compileCode();
    },
    immediate: true
  }
}
}
</script>


